	border: none;
	z-index: 1;
}

.button-pipaluk::before {
	position: absolute;
	content: '';
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: -1;
	transition: all .3s ease-in-out;
}

.button-pipaluk::after {
	position: absolute;
	content: '';
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	border-width: 2px;
	border-style: solid;
	opacity: 0;
	visibility: hidden;
	transition: all .2s ease 0s;
	transform: scale(1.05);
	will-change: transform;
