.boxs {
    position: relative;
    margin-bottom: 30px;
    color: #4d585f;
    background-color: white;
    filter: alpha(opacity=100);
    opacity: 1;
    -webkit-transition: opacity 0.25s ease-out;
    -moz-transition: opacity 0.25s ease-out;
    transition: opacity 0.25s ease-out;
    border-radius: 3px;
    text-align: center;
    align-items: center;
    transition: .2s;
}

.boxs:hover {
    transform: scale(1.05);
    box-shadow: 4px 5px 5px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 4px 5px 5px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 4px 5px 5px 0px rgba(0, 0, 0, 0.75);
}

.project_widget .pw_img {
    position: relative;
    overflow: hidden
}

.project_widget .pw_img:before {
    position: absolute;
    top: 0;
    left: -75%;
    z-index: 2;
    display: block;
    content: '';
    width: 50%;
    height: 100%;
    background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
    -webkit-transform: skewX(-25deg);
    transform: skewX(-25deg)
}

.project_widget .pw_img:hover::before {
    -webkit-animation: shine .75s;
    animation: shine .75s
}

@-webkit-keyframes shine {
    100% {
        left: 125%
    }
}

@keyframes shine {
    100% {
        left: 125%
    }
}

.project_widget .pw_img img {
    border-radius: 3px 3px 0 0
}

.project_widget .pw_content .pw_header {
    text-align: center;
    align-items: center;
    padding: 20px;
    border-bottom: 1px solid #e6eaee
}

.project_widget .pw_content .pw_header h6 {
    font-size: 16px;
    margin: 0;
    color:var(--red);
}

.project_widget .pw_content .pw_header small {
    font-size: 12px
}

.project_widget .pw_content .pw_header>span {
    color: var(--red);
    font-size: 5em;
}

.project_widget .pw_content .pw_header>p {
    color: var(--red);
    font-weight: 600;
}

.project_widget .pw_content .pw_meta {
    padding: 20px
}
.card-icon{
    color: var(--red);
    font-size: 1.5em;
    margin: .2em .5em;
}
.project_widget .pw_content .pw_meta small,
.project_widget .pw_content .pw_meta span {
    display: block
}

.project_widget .pw_content .pw_meta span {
    font-weight: 500;
    color:var(--red);
}
.buy--btn{
    color: var(--red);
}
.buy--btn:hover{
     transform: scaleX(1.1);
}